import {reqCartList,reqDeleteCartById,reqUpdateCheckById} from '@/api'

const state = {
    cartList:[],
};
const mutations ={
    GETCARTLIST(state,cartList){
        state.cartList = cartList;
    }
};
const actions = {
    async getCartList({commit}){
        let result = await reqCartList();
        // console.log(result);
        if (result.code == 200) {
            commit("GETCARTLIST",result.data);
        }
    },
    async deleteCartListById({commit},skuId){
        let result = await reqDeleteCartById(skuId);
        if (result.code == 200) {
            return 'okkkk;'
        }else{
            return Promise.reject(new Error('faile'));
        }
    },
    // 
    async updateCheckById({commit},{skuId,isChecked}){
        let result = await reqUpdateCheckById(skuId,isChecked);
        if (result.code == 200) {
            return 'ok'
        }else{
            return Promise.reject(new Error('faile'));
        }
    },
    // 
    deleteAllCheckedCart({dispatch,getters}){
        //context :commitmutationsstate gettersdispatchaction state
        // )
        let PromiseAll = [];
        getters.cartList.cartInfoList.forEach(item => {
            let promise = item.isChecked==1?dispatch('deleteCartListById',item.skuId):'';
            // promise
            PromiseAll.push(promise);
        });
        //p1|p2....
        //,
        return Promise.all(PromiseAll);
    },
    // 
    updateAllCartIsChecked({dispatch,state},isChecked){
        // 
        let PromiseAll = [];
        state.cartList[0].cartInfoList.forEach(item => {
            let promise = dispatch("updateCheckById",{skuId:item.skuId,isChecked});
            PromiseAll.push(promise);
        });
        // 
        return Promise.all(PromiseAll);
    }
};

const getters = {
    cartList(state){
        return state.cartList[0] || {}
    }
};

export default {
    state,
    mutations,
    actions,
    getters
}