<template>
  <div>
    <header class="header">
    <!--  -->
    <div class="top">
        <div class="container">
        <div class="loginList">
            <p></p>
            <!--   -->
            <p v-if="!userName">
            <span></span>
            <!-- to -->
            <router-link to="/login"></router-link>
            <router-link to="/register" class="register"></router-link>
        </p>
        <p v-else>
          <a>{{userName}}</a>
          <a class="register" @click="logout"></a>
        </p>

        

        </div>
        <div class="typeList">
            <router-link to="/center/myorder"></router-link>
            <router-link to="shopcart"></router-link>
            <a href="###"></a>
            <a href="###"></a>
            <a href="###"></a>
            <a href="###"></a>
            <a href="###"></a>
            <a href="###"></a>
        </div>
        </div>
    </div>
    <!-- -->
    <div class="bottom">
        <h1 class="logoArea">
        <router-link class="logo" title="" to="/home">
            <img src="./images/logo.png" alt />
        </router-link>
        </h1>
        <div class="searchArea">
        <form action="###" class="searchForm">
            <input type="text" id="autocomplete" class="input-error input-xxlarge" v-model="keyword"/>
            <button class="sui-btn btn-xlarge btn-danger" type="button" @click="goSearch"></button>
        </form>
        </div>
    </div>
    </header>
  </div>

</template>

<script>
    export default {
        name:'',
        data() {
          return {
            keyword:'',
          }
        },
        methods: {
          // search
          goSearch(){
            // 
            // 
            // this.$router.push('/search/' + this.keyword + "?k=" + this.keyword.toUpperCase())
            // 
            // this.$router.push(`/search/${this.keyword}?k=${this.keyword.toUpperCase()}`)
            // 
            // this.$router.push({name:'search',params:{keyword:this.keyword},query:{k:this.keyword.toUpperCase()}})
            //
            if(this.$route.query){
              let location = {name:"search",params:{keyword:this.keyword || undefined}};
              location.query = this.$route.query;
              this.$router.push(location);
            }
          },
          // 
          //1::token
          //2:userInfo. token
          logout(){
            try {
              // 
              this.$store.dispatch('userLogout');
              // 
              this.$router.push('/home');
            } catch (error) {
              
            }
          }
        },
        mounted() {
          // 
          this.$bus.$on("clear",()=>{
            this.keyword = '';
          })
        },
        computed:{
          // 
          userName(){
            return this.$store.state.user.userInfoff.name;
          }
        }
    }
</script>

<style scoped lang="less">
    .header {
  &>.top {
    background-color: #eaeaea;
    height: 30px;
    line-height: 30px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
          }
        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;

          &+a {
            border-left: 1px solid #b3aeae;
          }
        }

      }

    }
  }

  &>.bottom {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {
        img {
          width: 175px;
          margin: 25px 45px;
        }
      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 490px;
          height: 32px;
          padding: 0px 4px;
          border: 2px solid #ea4a36;
          float: left;

          &:focus {
            outline: none;
          }
        }

        button {
          height: 32px;
          width: 68px;
          background-color: #ea4a36;
          border: none;
          color: #fff;
          float: left;
          cursor: pointer;

          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}

</style>