<template>
  <div class="register-container">
    <!--  -->
    <div class="register">
      <h3>
        
        <span class="go">
          
          <a href="login.html" target="_blank"></a>
        </span>
      </h3>
      <div class="content">
        <label>:</label>
        <input placeholder="" v-model="phone" name="phone" v-validate="{required: true,regex:/^1\d{10}$/}" :class="{ invalid: errors.has( 'phone')}" />
        <span class="error-msg">{{ errors.first("phone")}}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input placeholder="" v-model="code" name="code" v-validate="{required: true,regex:/^\d{6}$/}" :class="{ invalid: errors.has( 'code')}" />
        <button style="width:100px;height:38px" @click="getCode()"></button>
        <span class="error-msg">{{ errors.first("code")}}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input placeholder="" v-model="password" name="password" v-validate="{required: true,regex:/^[0-9A-Za-z]{8,20}$/}" :class="{ invalid: errors.has( 'password')}" />
        <span class="error-msg">{{ errors.first("password")}}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input placeholder="" v-model="password1" name="password1" v-validate="{required: true,is:password}" :class="{ invalid: errors.has( 'password1')}" />
        <span class="error-msg">{{ errors.first("password1")}}</span>
      </div>
      <div class="controls">
        <input type="checkbox" v-model="agree" name="agree" v-validate="{required: true,'agreeagree':true}" :class="{ invalid: errors.has( 'agree')}" />
        <span></span>
        <span class="error-msg">{{ errors.first("agree")}}</span>
      </div>
      <div class="btn">
        <button @click="userRegister"></button>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address">6</div>
      <div class="beian">ICP19006430</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      // ---
      phone: "",
      // 
      code: "",
      // 
      password: "",
      // 
      password1: "",
      // 
      agree: true
    };
  },
  methods: {
    async getCode() {
      try {
        const { phone } = this; //const phone = this.state.phone;
        phone && (await this.$store.dispatch("getCode", phone));
        this.code = this.$store.state.user.code;
      } catch (error) {
        alert(error.message);
      }
    },
    // 
    async userRegister() {
      const success = await this.$validator.validateAll();
        if (success) {
          try {
          // 
          const { phone, code, password, password1 } = this;
          await this.$store.dispatch("userRegister", { phone, code, password });
          this.$router.push('/login');
        } catch (error) {
          alert(error.message);
        }
        }
        
    },
    
  }
};
</script>

<style lang="less" scoped>
